<style lang="scss">@import "./css/style.scss";</style>
<template>
    <div id="articleEdit">
        <el-card class="box-card">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-position="left" class="g-edit-form" label-width="160px">
                <el-form-item label="文章标题" prop="name">
                    <el-input v-model="ruleForm.name"></el-input>
                </el-form-item>
                <el-form-item label="文章内容" prop="region">
                    <RichText :height="200" v-model="ruleForm.content"/>
                </el-form-item>
                <el-form-item label="文章封面" prop="desc">
                    <el-image
                        style="width: 200px; height: 200px"
                        src=""
                        fit="contain"></el-image>
                </el-form-item>
                <el-form-item label="文章导航" prop="region">
                    <el-select v-model="ruleForm.region" placeholder="请选择文章导航">
                        <el-option label="区域一" value="shanghai"></el-option>
                        <el-option label="区域二" value="beijing"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="文章来源" prop="region">

                </el-form-item>
                <el-form-item label="标签" prop="region">
                    <SelectTags v-model="labelList" />
                </el-form-item>
                <el-form-item label="发布时间" prop="name">
                    <el-date-picker
                        v-model="ruleForm.name"
                        type="datetime"
                        placeholder="选择日期时间">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="课程搜索" prop="region">
                    <el-select
                        class="search-course"
                        v-model="ruleForm.name"
                        filterable
                        remote
                        reserve-keyword
                        placeholder="请输入关键词"
                        :remote-method="remoteMethod"
                        :loading="loading">
                        <div slot="prefix" style="padding: 0 10px">
                            <el-switch
                                v-model="isSearchAtomCourse"
                                active-text="搜索原子课程"
                                inactive-text="搜索课程包">
                            </el-switch>
                        </div>
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="">提交</el-button>
                </el-form-item>
            </el-form>
        </el-card>
    </div>
</template>

<script>
    import RichText from '@/components/RichText/RichText.vue'
    import SelectTags from '@/components/SelectTags/SelectTags.vue';
    export default {
        data() {
            return {
                isSearchAtomCourse:true,


                ruleForm:{},
                labelList:[],
                options:[],
                loading:true,
                rules: {
                    name: [
                        { required: true, message: '请输入活动名称', trigger: 'blur' },
                        { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                    ],
                    region: [
                        { required: true, message: '请选择活动区域', trigger: 'change' }
                    ],
                    date1: [
                        { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
                    ],
                    date2: [
                        { type: 'date', required: true, message: '请选择时间', trigger: 'change' }
                    ],
                    type: [
                        { type: 'array', required: true, message: '请至少选择一个活动性质', trigger: 'change' }
                    ],
                    resource: [
                        { required: true, message: '请选择活动资源', trigger: 'change' }
                    ],
                    desc: [
                        { required: true, message: '请填写活动形式', trigger: 'blur' }
                    ]
                }
            }
        },
        methods: {
            remoteMethod(query) {
                if (query !== '') {
                    this.loading = true;
                    setTimeout(() => {
                        this.loading = false;
                        this.options = this.rules.filter(item => {
                            return item.label.toLowerCase()
                                .indexOf(query.toLowerCase()) > -1;
                        });
                    }, 200);
                } else {
                    this.options = [];
                }
            }
        },
        created() {
        },
        components: {
            RichText,SelectTags
        }
    }
</script>
